<template>
  <div>
    <mt-header fixed title="固定在顶部"></mt-header>
    <div style="height:40px"></div>
    <router-link v-for="file in fileList" :to="'/file/'+file" :key="file" icon="more" slot="right">
      <mt-cell :title="file"></mt-cell>
    </router-link>
  </div>
</template>

<script>
import { MessageBox } from "mint-ui";
import axios from "axios";
import CryptoJS from "crypto-js";
import aes from '../js/aes.js';
import keyManager from '../js/keyManager.js';

export default {
  name: "list",
  data() {
    return {
      fileList: [],
      skey: '',
      typeKeyCount: 0
    };
  },
  methods: {
    checkKey: function () {
      const ckey = this.getCkey()
      if (!ckey) return false
      if (aes.encrypt(ckey, ckey) != this.skey)
        return false
      return true
    },
    setKey: function () {
      return new Promise(s => {
        let msg = ''
        if (this.typeKeyCount <= 0) msg = '请输入密码'
        else if (this.typeKeyCount == 1) msg = '密码错误, 请重新输入'
        else msg = `密码错误, 请重新输入 <br/>the ${this.typeKeyCount} attempt`
        this.typeKeyCount++
        MessageBox.prompt(msg).then(({ value, action }) => {
          keyManager.setKey(value);
          s()
        })
      })
    },
    getCkey: function () {
      return keyManager.getKey()
    },
  },
  mounted: async function () {
    let m = await axios.get("./static/data/list.json")
    //验证密码
    this.skey = m.data.key
    while (!this.checkKey()) {
      await this.setKey()
    }
    this.fileList = m.data.files;
  }
};
</script>

 <style scoped>
a {
  color: #42b983;
  text-decoration: none;
}
</style>



